import React from "react";
import "./work.css";
import $ from 'jquery';

function Review({ counter, called }) {
  const exp = [
    {
      title: "Project Intern",
      subscript: "at a Stealth Startup",
      about:
        "Here, my role was to work on the production app's UI. Developed and worked on 16+ core components and also improved the existing UI experience using React and its hooks. I was also able to experience AWS cloud computing and its services like DynamoDB, Lambdas, and much more. I got to work with Azure DevOps adn its features like, git version control, workitems, sprints to track team progress. Was my first Internship!",
      tech: "React, AWS, SpringBoot, Git, Azure, Postman, Debugging",
    },
    {
      title: "Technology Intern",
      subscript: "at Edufund",
      about:
        "Here, I work on the core frontend made with react-native. My day to day tasks include, making API's, reading about which library is best suited for upcoming requirements, designing screens, fixing bugs etc.",
      tech: "React-Native and its various Libraries, State Management",
    },
    {
      title: "Core NGO member",
      subscript: "at AYUDA",
      about:
        "Here we focus on the upbringing of the underprivileged girl children in Vellore by not only through physical means like books, clothes, general utils but also through regular meets/seminars where these children attend and interact with the host of their choice. WE try our best to provide them with the best platform for them to have a real glimpse of the world and also find their areas of interest.",
    },
    {
      title: " Ex-Managers Say: ",
      about: `" Shamith’s ability to understand the entre functionality and design the pieces he did, using his technical expertise was remarkably mature for an intern. "`,
    },
    {
      title: "Ex-Managers Say:",
      about: `" Shamith is curious and would probe to get a proper understanding so that he always has the big picture and then would design his components to fit that understanding. He is detail-oriented and has the ability to think the problem through to its conclusion. A fantastic trait at an analyst level, he has the makings of being a product design lead or an engineer lead who truly understands the business side of things as well. "`,
    },
    {
      title: "Hack-a-thons!",
      subscript: "9 participations",
      about: `2nd Place at AppTitute Hack by coding Blocks`,
    },
    {
      title: "Hack-a-thons!",
      subscript: "9 participations",
      about: `Runner up at EnPhython-2019`,
      link:"https://drive.google.com/file/d/1XXWAmtOpbf_mkkQ1vx3HQJWIjbDexz6R/view?usp=sharing"
    },
    {
      title: "4 Star Coder",
      subscript: "CodeChef",
      about: "Love Data Structures :)",
    },
  ];
  
  console.log("COMPONENT CALLEDdddd");
  //   document.getElementById("divOne").classList += "animate";

  React.useEffect(() => {
    // debugger;
    if (Boolean(document.getElementById("divOne") != undefined)) {
      if (Boolean(document.getElementById("divOne").classList[1])) {
        $('#divOne').removeClass('animate');
      }

      setTimeout(()=>$('#divOne').addClass('animate'),1);
    }
  }, [counter]);

  //   document.getElementById('review-container').style.animation = "anim2 2s 2s forward";
  return (
    <div id="divOne" className="review-container">
      <a className="work-heading ">{exp[counter].title}</a>
      {exp[counter].subscript && (
        <a className="work-subscript">~ {exp[counter].subscript}</a>
      )}
      <a className="work-about">{exp[counter].about}</a>
      {exp[counter].tech && <a className="work-tech-heading">Skills Learnt:</a>}
      {exp[counter].tech && <a className="work-tech">{exp[counter].tech}</a>}
      {/* {animate()} */}
    </div>
  );
}

export default Review;
