import React from "react";
import Header from "../header";
import Body from "../body";
import Footer from "../footer";
import { useState } from "react";
import "./home.css";
import LeftTat from "../leftTat";

export default function Main() {
  const [ sidebar, setSidebar ] = useState(false);
  return (
    <div className="main"> 
      <section>
        <LeftTat></LeftTat>
      </section>
      <section>
        <Header sidebar={sidebar} setSidebar={setSidebar}></Header>
      </section>
      <section onClick={()=>{if(sidebar){ setSidebar(false)}}}>
        <Body ></Body>
      </section>
      <div style={{backgroundColor:"rgb(38,38,38)",display:"flex",flexDirection:"column",alignItems:"center"}}>
        <Footer onClick={()=>{if(sidebar){setSidebar(false)}}}></Footer>
      </div>
    </div>
  );
}
