import React from "react";
import Review from "./Review";
import "./work.css";
import { BsArrowLeft, BsArrowRight } from "react-icons/bs";

let NUMBEROFEXP=8;

function Work() {


    const increaseCounter=()=>{
        let temp=counter;
        if(counter===NUMBEROFEXP-1){
            temp=0;
        }else{
            temp=counter+1;
        }
        setCounter(temp);
    }
    const decreaseCounter=()=>{
        let temp=counter;
        if(counter===0){
            temp=NUMBEROFEXP-1;
        }else{
            temp=counter-1;
        }
        setCounter(temp);
    }
  const [counter, setCounter] = React.useState(0);
  const [called,setCalled]=React.useState(0);
  return (
    <div className="work-container">
      <div className="work-header">
        Some of my Accomplishments and
        <p className="domain">EXPERIENCE</p>
      </div>
      <div className="exp-changer-container">
        <div className="left-arrow" onClick={decreaseCounter}>
          <BsArrowLeft size={"2em"}></BsArrowLeft>
        </div>
        <div className="right-arrow" onClick={increaseCounter}>
          <BsArrowRight size={"2em"}></BsArrowRight>
        </div>
      </div>
      <div className="reviews-container">
        <Review
        // called={called}
          counter={counter}
        ></Review>
        {/* {setCalled(called+1)} */}
      </div>
      
    </div>
  );
}

export default Work;
